<template style="height:100%">
  <div class="search-model-essay">
    <!-- 搜索框 -->
    <div class="card1">
      <div class="searching w">
        <!-- 搜索模块 -->
        <div class="search">
          <!-- <el-input placeholder="请输入内容" v-model="value" clearable @input="searchAction($event)" ></el-input> -->
          <el-input placeholder="请输入内容" v-model="value" clearable @input="searchAction($event)"
            style=" width: 1.9417rem;height: 0.2548rem;font-size: 0.09375rem;"></el-input>
          <!-- <input id='write' type="text" value="请输入关键词" @input="searchAction($event)"> -->
          <button @click="getData()" :disabled="isDisable"><a href="#"><img src=".//img/搜索框.png"></a></button>
        </div>
      </div>
      <div class="all_result">
        <div v-for="item in teacher" :key="item.id" class="result">
          <div><a :href="item.article_url" target="_blank">{{item.title}}</a></div>
          <div class="authors">作者： {{item.authors}}</div>
          <div class="company_list">机构： {{item.company_list}}</div>
          <div class="keywords">关键词： {{item.keywords}}</div>
        </div>
      </div>
      <div v-if="show1">
        <nav style="text-align: center;">
          <ul class="pagination" @click="backtop">
            <li @click="prePage()" class="prev">
              <a>&laquo; 上一页</a>
            </li>
            <!-- page.pageNo==index?'active':'' -->
            <li :class="page.pageNo==index?'active':''" v-for="index in pages" :key="index" @click="curPage(index)">
              <a>{{index}}</a>
            </li>
            <li @click="nextPage()" class="next">
              <a>下一页 &raquo;</a>
            </li>
          </ul>
        </nav>
      </div>
    </div>
    <div class="panel pie"
      style="{width: '2.0833rem', height: '2.0833rem', padding: '0.1302rem',margin-bottom:'0.26rem'}">
      <h2 style="text-align: center; color: #303030;font-size: 0.16375rem;font-weight: 700;">主题词词云</h2>
      <div id="chart" ref="chart"></div>
      <div class="panel-footer"></div>
    </div>
    <div class="panel1 bar"
      style="{width: '2.0833rem', height: '2.0833rem', padding: '0.1302rem',margin-top:'0.26rem'}">
      <h2 style="text-align: center; color: #222222;font-size: 0.16375rem;font-weight: 700;">
        各类别主题数
      </h2>
      <div id="chart1"></div>
      <div class="panel-footer"></div>
    </div>
    <router-view></router-view>
  </div>
</template>
// <script src="jquery-3.1.1.min.js" type="text/javascript"></script>
<script type="text/javascript">
  import Axios from 'axios'
  import $ from 'jquery'
  import "echarts-wordcloud/dist/echarts-wordcloud";
  import "echarts-wordcloud/dist/echarts-wordcloud.min";
  export default {
    data() {
      return {
        teacher: [],
        page: {
          num_pages: 1, //总页数
          count: 1, //总条数
          pageNo: 1, //页码
          pageSize: 8, //每一页条数
        },
        isDisable: false,
        show1: false,
        value: ''
      };
    },
    computed: {
      pages() {
        let {
          pageNo: n,
          num_pages: total
        } = this.page;
        if (total < 10) return total; //页数小于10页  省略号不添加
        if (n <= 5) {
          return [1, 2, 3, 4, 5, 6, '...', total];
        } else if (n >= total - 5) {
          return [1, '...', total - 6, total - 5, total - 4, total - 3, total - 2, total - 1, total]
        } else {
          return [1, '...', n - 2, n - 1, n, n + 1, n + 2, '...', total]
        }

      }
    },
    //   mounted() {
    //   this.searchAction();
    //   this.ldaAnalysis();
    //   this.getData();
    // //   this.getWenxian();
    //   },
    //    name: "",
    methods: {
      searchAction(e) {
        var inputvalue = this.value;
        window.inputvalue = inputvalue;
      },
      //    getWordcloud1() {
      //         var inputvalue1 = this.value;
      //         // var dom = document.getElementById("chart");
      //         var myChart11 = this.$echarts.init(this.$refs.chart);
      //         var app = {};
      //         var option = null;
      //         myChart11.showLoading();
      //         Axios.get("http://124.223.32.208:8000/api/searching/lda_analysis?category=periodical&keywords=碳")
      //         .then((response) =>{
      //         console.log(response.data),
      //         function() {
      //             let JsonList = response.data;
      //             var JsonList1 =[];
      //             var a = JsonList.words_cloud;
      //                 for (word in a) {
      //                     JsonList1.push(word)
      //                 }
      //             console.log(JsonList1)
      //             myChart11.hideLoading();
      //             myChart11.setOption(
      //                 option = {
      //                     tooltip: {
      //                         show: true
      //                     },
      //                     series: [{
      //                         name: '纺织技术词云',
      //                         type: 'wordCloud',
      //                         sizeRange: [20, 70], //文字范围
      //                         //文本旋转范围，文本将通过rotationStep45在[-90,90]范围内随机旋转
      //                         rotationRange: [-45, 90],
      //                         rotationStep: 45,
      //                         textRotation: [0, 45, 90, -45],
      //                         // gridSize: 9, 
      //                         //形状
      //                         shape: 'circle',
      //                         textStyle: {
      //                             normal: {
      //                                 color: function() { //文字颜色的随机色
      //                                     return 'rgb(' + [
      //                                         Math.round(Math.random() * 250),
      //                                         Math.round(Math.random() * 250),
      //                                         Math.round(Math.random() * 250)
      //                                     ].join(',') + ')';
      //                                 }
      //                             },
      //                             //悬停上去的字体的阴影设置
      //                             emphasis: {
      //                                 shadowBlur: 10,
      //                                 shadowColor: '#333'
      //                             }
      //                         },
      //                         data: JsonList
      //                     }]
      //                 }
      //             );
      //         // }
      //         }});
      //    },
      getData(i = this.page.pageNo) {
        this.page.pageNo = i;
        // var inputvalue = this.write;
        // this.Axios({
        //     method:'get',
        //     url:'http://124.223.32.208:8000/api/searching/get_periodical?keywords=this.write&page=${this.page.pageNo}'
        // })
        Axios.get('http://124.223.32.208:8000/api/searching/get_periodical?keywords=' + inputvalue + '&page=' + this
            .page.pageNo)
          .then((response) => {
            console.log(response.data)
            let {
              list,
              num_pages,
              count
            } = response.data;
            // if (response.data.list.length != 0) {
            // var pages = response.data.page_range
            // var count = response.data.num_pages
            // for (pages < count;pages++;) {
            this.teacher = list;
            this.page.num_pages = num_pages;
            console.log(this.teacher)
            console.log(this.page.num_pages)
            console.log(this.page.count)
            this.page.count = count;
            this.isDisable = true
            setTimeout(() => {
              this.isDisable = false
            }, 1000)
          })
        this.show1 = true;
        this.ldaAnalysis();
      },
      curPage(i) { //当前页
        if (i == '...') return;
        this.getData(i);
      },
      prePage() { //上一页
        let {
          pageNo
        } = this.page;
        if (pageNo > 1) {
          this.getData(--pageNo)
        }
      },
      nextPage() { //下一页
        let {
          pageNo,
          num_pages
        } = this.page;
        if (pageNo < num_pages) {
          this.getData(++pageNo)
        }
      },
      backtop() {
        let top = document.documentElement.scrollTop || document.body.scrollTop
        // 实现滚动效果
        const timeTop = setInterval(() => {
          document.body.scrollTop = document.documentElement.scrollTop = top -= 50
          if (top <= 0) {
            clearInterval(timeTop)
          }
        }, 10)
      },
      ldaAnalysis() {
        var dom = document.getElementById("chart");
        var myChart11 = this.$echarts.init(dom);
        var option = null;
        let dom1 = document.getElementById("chart1");
        let myChart = this.$echarts.init(dom1);
        let option1 = null;
        console.log(this.value);
        myChart11.showLoading({
          text: '正在进行主题分析,请耐心等待...', //加载时候的文本
          color: 'lightblue', //加载时候小圆圈的颜色
          textColor: 'white', //加载时候文本颜色
          maskColor: '#082042', //加载时候的背景颜色
        });
        myChart.showLoading({
          text: '正在进行主题分析,请耐心等待...', //加载时候的文本
          color: 'lightblue', //加载时候小圆圈的颜色
          textColor: 'white', //加载时候文本颜色
          maskColor: '#082042' //加载时候的背景颜色
        });
        $.get("http://124.223.32.208:8000/api/searching/lda_analysis?category=periodical&keywords=" + this.value,
          function(data) {
            // $.get("http://124.223.32.208:8000/api/searching/lda_analysis?category=periodical&keywords=" + inputvalue)
            // .then((response) =>{
            // console.log(response.data)
            let JsonList = [];
            for (let word of data.words_cloud) {
              JsonList.push(word)
            }

            myChart11.hideLoading();
            myChart11.setOption(
              option = {
                tooltip: {
                  show: true
                },
                series: [{
                  name: '纺织技术词云',
                  type: 'wordCloud',
                  sizeRange: [20, 70], //文字范围
                  //文本旋转范围，文本将通过rotationStep45在[-90,90]范围内随机旋转
                  rotationRange: [-45, 90],
                  rotationStep: 45,
                  textRotation: [0, 45, 90, -45],
                  // gridSize: 9,
                  //形状
                  shape: 'circle',
                  textStyle: {
                    normal: {
                      color: function() { //文字颜色的随机色
                        return 'rgb(' + [
                          Math.round(Math.random() * 250),
                          Math.round(Math.random() * 250),
                          Math.round(Math.random() * 250)
                        ].join(',') + ')';
                      }
                    },
                    //悬停上去的字体的阴影设置
                    emphasis: {
                      shadowBlur: 10,
                      shadowColor: '#333'
                    }
                  },
                  data: JsonList
                }]
              }
            );
            var datas = data.topic_statistics;
            let x_data = [];
            let y_data = [];
            let words = []
            for (let c of datas) {
              x_data.push(c.category);
              y_data.push(c.count);
              let words_list = c.words.split(" ")
              let front = words_list.slice(0, 5).join(" ");
              let last = words_list.slice(5).join(" ");
              let new_words = front + "</br>" + last;
              words.push(new_words);
            }
            myChart.hideLoading();
            myChart.setOption(
              option1 = {
                color: ["#2f89cf"],
                tooltip: {
                  show: true,
                  formatter: function(params) {
                    return params.name + "：" + params.data + "<br/>" + words[parseInt(params.name.slice(2)) - 1];
                  }
                },
                grid: {
                  left: "5px",
                  top: "10px",
                  right: "0%",
                  bottom: "4%",
                  containLabel: true
                },
                xAxis: [{
                  type: "category",
                  data: x_data,
                  axisTick: {
                    alignWithLabel: true
                  },
                  axisLabel: {
                    textStyle: {
                      // color: "rgba(255,255,255,.6)",
                      color: "#5b5b5b",
                      fontSize: "16"
                    },
                    rotate: 60,
                    interval: 0, //代表显示所有x轴标签显示
                  },
                  axisLine: {
                    show: false
                  }
                }],
                yAxis: [{
                  type: "value",
                  axisLabel: {
                    textStyle: {
                      // color: "rgba(255,255,255,.6)",
                      color: "#5b5b5b",
                      fontSize: "16"
                    }
                  },
                  axisLine: {
                    lineStyle: {
                      color: "rgba(255,255,255,.1)"
                      // width: 1,
                      // type: "solid"
                    }
                  },
                  splitLine: {
                    lineStyle: {
                      color: "rgba(255,255,255,.1)"
                    }
                  }
                }],
                series: [{
                  name: "文献数量",
                  type: "bar",
                  barWidth: "55%",
                  data: y_data,
                  itemStyle: {
                    barBorderRadius: 5
                  }
                }]
              }
            );
          });
      },
    }
  }
</script>


<style scoped lang="less">
