    width: 5.833rem;
    height: 4.6875rem;
    //   display: flex;
    //   justify-content: center;
  }

  .result {
    float: left;
    width: 2.7083rem;
    height: 0.9875rem;
    margin-top: 0.09rem;
    margin-left: 0.066rem;
    font-size: 0.1042rem;
    font-weight: 700;
